-style:none}.country_data .country_info ul li{margin:12px 0;color:rgba(255,255,255,0.6)}.country_data .country_icon{margin:2rem 0;text-align:center}.primary-btn{border:none;outline:none;padding:1rem;color:#fff;background:#ff1f2d;border-radius:20px;transition:all 0.4s;cursor:pointer}.primary-btn:hover{background:#ff0615}.graph_container{height:100vh;background:rgba(255,255,255,0.11);border-radius:20px;padding:1rem;color:#fff}.graph_container>*{height:100%;width:50%}.spinner{width:2rem;height:2rem;margin-right:1rem;border:3px solid transparent;border-top-color:#ff1f2d;border-bottom-color:#ff1f2d;border-radius:50%;-webkit-animation:rotation 0.8s ease infinite;animation:rotation 0.8s ease infinite}@-webkit-keyframes rotation{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}@keyframes rotation{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}.map_container{position:fixed;width:100%;height:100vh}#map{position:absolute;top:0;bottom:0;width:100%;height:100%}.marker-btn{border:none;background:transparent}.circle{height:20px;width:20px;border-radius:100%;background:rgba(255,31,45,0.2);border:2px solid #ff1f2d}.popup{padding:0.5rem;text-align:center}.popup ul{list-style:none}.popup img{width:80%}.mapboxgl-popup-close-button{font-size:2rem;padding:0;margin-right:10px}.modal-bg{height:100vh;width:100%;position:absolute;top:0;left:0;right:0;bottom:0;background:rgba(0,0,0,0.5);display:flex;justify-content:center;align-items:center}.modal-bg .modal-content{position:relative;color:#fff;background:#222;padding:3rem 1rem;border-radius:20px;text-align:center}.modal-bg .modal-content .close-modal{color:#fff;position:absolute;right:10px;top:1rem;font-size:1.2rem;text-decoration:none;font-weight:bold}.modal-bg .modal-content h1{margin-bottom:2rem}.modal-bg .modal-content button{width:100%;margin-top:1rem}.top_affected_countries .countries{display:grid;grid-template-columns:repeat(auto-fit, minmax(240px, 1fr));grid-gap:1rem}.top_affected_countries .countries .country{transi